// app/components/features/user-selection/SelectionCard.tsx
'use client'; // NecessÃ¡rio para a prop onClick

import { BarberPoleIcon } from '@/app/components/icons/BarberPoleIcon';

type Props = {
  title: string;
  subtitle: string;
  variant: 'client' | 'barbershop';
  onClick: () => void;
};

export const SelectionCard = ({ title, subtitle, variant, onClick }: Props) => {
  // Map variant to concrete Tailwind classes so the JIT can detect them
  const bgClass = variant === 'client' ? 'bg-barber-client' : 'bg-barber-shop';

  return (
    <button
      onClick={onClick}
      type="button"
      className={`
        ${bgClass} 
        w-64 h-64 sm:w-80 sm:h-80 md:w-96 md:h-96
        rounded-2xl 
        flex flex-col justify-center items-center 
        text-center p-6 
        cursor-pointer 
        transition-transform duration-200 hover:scale-105 focus:outline-none focus:ring-4 focus:ring-barber-primary/20
      `}
      aria-label={subtitle}
    >
      <span className="text-barber-primary/80 text-lg md:text-xl font-semibold whitespace-pre-line leading-tight">
        {title}
      </span>

      <div className="w-20 h-20 bg-barber-circle rounded-full my-6 flex items-center justify-center">
        {/* Icon uses currentColor so we tint it with text-barber-primary */}
        <BarberPoleIcon className="w-10 h-10 text-barber-primary" />
      </div>

      <span className="text-barber-primary text-xl md:text-2xl font-bold">
        {subtitle}
      </span>
    </button>
  );
};
